import React from "react";
import "./App.css";
import NavBar from "./components/commons/NavBar";
import DailyCalories from "./components/DailyCalories";
import Exercise from "./components/Exercise";
import Health from "./components/Health";
import Today from "./components/Today";
import { Route, Switch } from "react-router-dom";
import config from "./components/config.json";
function App() {
  return (
    <React.Fragment>
      <NavBar />
      <div>
        <Switch>
          <Route path={config.root + "/today"} component={Today} />
          <Route path={config.root + "/addexercise"} component={Exercise} />
          <Route path={config.root + "/addweight"} component={Health} />

          <Route path={config.root + "/profile"} component={Health} />
          <Route path={config.root + "/"} component={Today} />
        </Switch>
      </div>
    </React.Fragment>
  );
}

export default App;
